<template lang="html">
  <el-table :data="tableData">
    <el-table-column prop="date" label="日期" width="140">
    </el-table-column>
    <el-table-column prop="name" label="姓名" width="120">
    </el-table-column>
    <el-table-column prop="address" label="地址">
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  data(){
    return {
      item:{
        date: '2018-11-11',
        name: 'one piece',
        address: '郑州市高新区推进城'
      },
      tableData: [],
      totalList:[],

    }
  },
  created () {
    this.tableData = Array(20).fill(this.item);

  },
  watch:{
     $route(to,from){
         this.item.name =this.$route.params.id;
     }
   },
}
</script>

<style lang="css">
</style>



